export default {
	data() {
		return {
			data:[]
		}
	},
	onLoad:function(){
		this.getList()
	},
	methods:{
		getList:function(){
			uni.request({
				url:this.$url+'unSubmitGoods',
				success:(res)=>{
					console.log(res)
					this.data=res.data.items;
				}
			})
		},
		navigate:function(goodsID){
			uni.navigateTo({
				url:'../goods-add/add1/add1?goodsID='+goodsID
			})
		}
	}
}
